<template>
    <v-container>
      
      <v-card class="mx-auto px-6 py-8 " max-width="344" color="deep-purple-darken-4">
        <v-form
          v-model="form"
          @submit.prevent="onSubmit"
        >
        <div class="text-subtitle-1 text-white">Account</div>  
        <v-text-field
            v-model="account"
            :readonly="loading"
            :rules="[required]"
            class="mb-2 "
            clearable
            placeholder="Enter your UserName"
          ></v-text-field>
          <div
        class="text-white text-subtitle-1  d-flex align-center justify-space-between"
      >
        Password

        <a
          class="text-caption text-decoration-underline text-white"
          href="#"
          rel="noopener noreferrer"
          target="_blank"
          
        >
          Forgot login Password?</a
        >
      </div>
          <v-text-field
            :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
            :type="visible ? 'text' : 'password'"
            v-model="password"
            :readonly="loading"
            :rules="[required]"
            @click:append-inner="visible = !visible"
            
            placeholder="Enter your password"
          ></v-text-field>
  
          <br>
  
          <v-btn
            :disabled="!form"
            :loading="loading"
            block
            color="success"
            size="large"
            type="submit"
            variant="elevated"
          >
            登入
          </v-btn>
        </v-form>
      </v-card>
    
    </v-container>
  </template>
  
  <script>
  import axios from 'axios';
  const CryptoJS = require("crypto-js");
    export default {
      data: () => ({
        form: false,
        salt:  "kowkoww151s5ww",
        account: null,
        password: null,
        token:'',
        visible: false,
        loading: false,
      }),
  
      methods: {
        onSubmit () {
          const saltedPassword = this.password + this.salt;
          const hashedPassword = CryptoJS.MD5(saltedPassword).toString();
          if (!this.form) return
          this.loading = true
          axios.post(
            "http://20.89.131.34:443/api/v1/account/login",
            {
              "username":this.account,
              "password":hashedPassword
            },
          )
          .then(res=> {
              console.log(res);
              this.loading = false;
              if(res.status == 200){
                const data = JSON.stringify(res.data.fishesID);
                localStorage.setItem("fish",data)
                localStorage.setItem("UserName",res.data.username)
                localStorage.setItem("UserEmail",res.data.email)
                localStorage.setItem("UserLevel",res.data.level)
                localStorage.setItem("UserSection",res.data.section)
                document.cookie = "token=" + res.data.token + "; path=/";
                window.location.replace(`http://20.89.131.34:443/static/dist/home`); //括號內加上+res.data.token http://20.89.131.34:443/static/dist/home
                //localStorage.getItem("fish")
                //JSON.parse
              }
              else
              alert("登入失敗")
          })
          .catch(err=> {
              console.log(err);
              this.loading = false;
              alert('登入失敗');
          })
        },
        required (v) {
        return !!v || 'Field is required'
      },
        
      }
    }
  </script>